
.product-area{
    padding: 10% 6% 3% 6%;
    display: flex;
    justify-content: space-between;
    
} 



.product-cell{
    min-width: 24%;
    max-width: 24%;
    min-height: 250px;
    transition: 80ms;
    max-height: 250px;
    margin-bottom: 18px;
    border-radius: 10px;
    border: 1px solid black;
}
.product-cell:hover{
    border: 3px solid #EF5350;
    border-radius: 13px;
}

.product-cell img{
    height: 160px;

}
.product-cell .display-content{
    color: black;
}


.panigation{
    /* border: 1px solid black; */
    width: 100%;
    display: flex;
    position: fixed;
    height: 50px;
    background-color: white;
    align-items: center;
    z-index: 3;
    bottom: 0px;
    
    justify-content: center;

}